import React from 'react';
import classes from './Pagination.module.css';

const pagination = ({ page–°ount, currentPage, choosePage }) => {
	const pages = [];
	const prevPage = () => {
		return currentPage - 1 > 0 ? currentPage - 1 : 1;
	};
	const nextPage = () => {
		return currentPage + 1 <= page–°ount ? currentPage + 1 : page–°ount;
	};
	// –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–µ —á–∏—Å–ª–æ –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º—ã—Ö –∫–Ω–æ–ø–æ–∫ - 5
	if (page–°ount <= 5 || currentPage <= 2) {
		// –µ—Å–ª–∏ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤—Å–µ—Ö —Å—Ç—Ä–∞–Ω–∏—Ü –º–µ–Ω—å—à–µ 5 –∏–ª–∏ –Ω–æ–º–µ—Ä —Ç–µ–∫—É—â–µ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã –º–µ–Ω—å—à–µ 2,
		// —Ç–æ –ø—Ä–æ—Å—Ç–æ –ø—Ä–∏—Å–≤–∞–∏–≤–∞–µ–º –º–∞—Å—Å–∏–≤—É pages –≤—Å–µ –≤–æ–∑–º–æ–∂–Ω—ã–µ –∑–Ω–∞—á–µ–Ω–∏—è, –Ω–æ –Ω–µ –±–æ–ª—å—à–µ 5
		for (let i = 1; i <= 5 && i <= page–°ount; i++) {
			pages.push(i);
		}
	} else if (currentPage >= page–°ount - 2) {
		// –µ—Å–ª–∏ –¥–æ –∫–æ–Ω—Ü–∞ —Å–ø–∏—Å—Ç–∞ –æ—Å—Ç–∞–ª–æ—Å—å –º–µ–Ω—å—à–µ 2 —Å—Ç—Ä–∞–Ω–∏—Ü,
		// —Ç–æ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º —Ç–æ–ª—å–∫–æ 5 –ø–æ—Å–ª–µ–¥–Ω–∏—Ö –Ω–æ–º–µ—Ä–æ–≤
		for (let i = page–°ount; i > page–°ount - 5; i--) {
			pages.push(i);
		}
		pages.reverse();
	} else {
		// –µ—Å–ª–∏ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤—Å–µ—Ö —Å—Ç—Ä–∞–Ω–∏—Ü –±–æ–ª—å—à–µ 5,
		// —Ç–æ –±–µ—Ä—ë–º –¥–≤–∞ –∑–Ω–∞—á–µ–Ω–∏—è –¥–æ —Ç–µ–∫—É—â–µ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã –∏ –¥–≤–∞ –∑–Ω–∞—á–µ–Ω–∏—è –ø–æ—Å–ª–µ
		for (let i = currentPage - 2; i <= currentPage + 2; i++) {
			pages.push(i);
		}
	}

	return (
		<nav className={classes.menu}>
			<ul>
				<li>
					<button className={classes.button} onClick={() => choosePage(1)}>
						&#60;&#60;
					</button>
				</li>
				<li>
					<button
						className={classes.button}
						onClick={() => choosePage(prevPage())}>
						&#60;
					</button>
				</li>
				{pages.map((page) => (
					<li key={page}>
						<button
							className={`${classes.button} ${
								page === currentPage ? classes.current : null
							}`}
							onClick={() => choosePage(page)}>
							{page}
						</button>
					</li>
				))}
				<li>
					<button
						className={classes.button}
						onClick={() => choosePage(nextPage())}>
						&#62;
					</button>
				</li>
				<li>
					<button
						className={classes.button}
						onClick={() => choosePage(page–°ount)}>
						&#62;&#62;
					</button>
				</li>
			</ul>
		</nav>
	);
};

export default pagination;
